
.btn {
	padding: 8px 12px;
	border: 2px solid lightgray;
	border-radius: 3px;
	margin: 0 10px;

	position: relative;
	overflow: hidden;
}

.btn:focus {
	outline: none;
	border: 2px solid brown;
}

.btn > .dot {
	width: 10px; height: 10px;
	border-radius: 50%;
	background: brown;
	opacity: 0.4;

	position: absolute;
	-webkit-animation: 0.5s big forwards;
	        animation: 0.5s big forwards;
}

@-webkit-keyframes big{
	0%{-webkit-transform: scale(0.1);transform: scale(0.1)}
	100%{-webkit-transform: scale(14);transform: scale(14)}
}

@keyframes big{
	0%{-webkit-transform: scale(0.1);transform: scale(0.1)}
	100%{-webkit-transform: scale(14);transform: scale(14)}
}



